const data = [
  {
    title: "Electronics",
    id: 1,
    subMenu: [
      {
        title: "smartphones",
        subMenu: [
          {
            title: "iPhone",
          },
          {
            title: "Samsung",
          },
        ],
      },
      // {
      //   title: "laptops",
      //   subMenu: [{ title: "MacBook Pro" }, { title: "Chromebook" }],
      // },
    ],
  },
  {
    title: "Consoles",
    id: 2,
    subMenu: [
      {
        title: "Playstation",
        subMenu: [{ title: "PS4" }, { title: "PS5" }],
      },
      {
        title: "Xbox",
        subMenu: [{ title: "Xbox 360" }, { title: "One X" }],
      },
    ],
  },
];
const menuItems = document.getElementById("categories");
const categories = data
  .map((element) => {
    if (element?.subMenu) {
      const subcategories = element.subMenu
        .map((subElement) => {
          return `<li key=${subElement?.id}><a href="#">${subElement.title}</a></li>`;
        })
        .join("");
      console.log("Submenu exists");
      const subMenuElement = document.getElementById("submenu");
      subMenuElement.insertAdjacentHTML("afterend", subcategories);
    }

    return `<li key=${element.id}><a href="#">${element.title}</a></li>`;
  })
  .join("");

const menuItemsElement = menuItems.insertAdjacentHTML("afterend", categories);

console.log(menuItems);
